  },
  methods: {
    checkStepForm1() {
      this.errors = [];
      if (this.email && this.password && this.passwordConfirmation) {
        this.isDisabled = false;
        return;
      }
      this.isDisabled = true;
    },
    checkStepForm2() {
      this.errors = [];
      if (this.name && this.nameCountry) {
        this.isDisabled = false;
        return;
      }
      this.isDisabled = true;
    },
    checkStepForm3() {
      this.isDisabled = false;
    },
    handleStep1() {
      this.errors = [];
      if (!this.email) {
        this.errors.push("E-mail  requerido.");
      } else if (!this.validEmail(this.email)) {
        this.errors.push('O e-mail deve ser vlido.');
      }
      if (this.password !== this.passwordConfirmation) {
        this.errors.push('A confirmao das senhas est incorreta.');
      }
      if (this.password.length < 6) {
        this.errors.push('A senha deve conter no mnimo 6 caracteres');
      }
      if (!this.errors.length) {
        this.activeStep++;
        this.isDisabled = true;
        this.checkStepForm2();
      }
    },
    handleStep2() {
      this.errors = [];
      if (this.phoneNumber && this.validNumber(this.phoneNumber)) {
        this.errors.push('O nmero de telefone deve ser vlido.');
      }
      if (!this.errors.length) {
        this.activeStep++;
        this.isDisabled = !this.accountType;
      }
    },
    handleStep3() {
      this.requestCreateUser();
    },
    validEmail: function (email) {
      const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    validNumber: function (number) {
      return isNaN(Number(number));
    },
    handlePreviousStep() {
      if (this.activeStep > 0) {
        this.errors = [];
        this.activeStep--;
        this.isDisabled = false;
      }
    },
    requestCreateUser() {
      this.isDisabled = true;
      this.isLoading = true;
      api.post('/api/v1/user', {
        email: this.email,
        password: this.password,
        name: this.name,
        country: this.nameCountry,
        phoneNumber: this.phoneNumber,
        accountType: this.accountType,
      })
          .then(() => {
            this.activeStep++;
          })
          .catch(error => {
            if (error.response !== undefined) {
              this.$buefy.toast.open({
                message: error.response.data.message,
                type: 'is-danger'
              });
              return;
            }
            this.$buefy.toast.open({
              message: error.toString(),
              type: 'is-danger'
            });
          }).finally(() => {
        this.isLoading = false;
        this.isDisabled = false;
      });
    }
  },
  computed: {
    filteredDataCountryArray() {
      return this.dataCountry.filter((option) => {
        return option
            .toString()
            .toLowerCase()
            .indexOf(this.nameCountry.toLowerCase()) >= 0
      })
    }
  }
