  methods: {
    checkFormMyData(e) {
      this.editUser();
      e.preventDefault();
    },
    checkFormChangePassword(e) {
      this.changePassword();
      e.preventDefault();
    },
    getUser() {
      this.isLoading = true;
      api.get('/api/v1/user/detail')
          .then(response => {
            this.email = response.data.email;
            this.name = response.data.name;
            this.nameCountry = response.data.country;
            this.phoneNumber = response.data.phoneNumber;
          })
          .catch(error => {
            if (error.response !== undefined) {
              this.$buefy.toast.open({
                message: error.response.data.message,
                type: 'is-danger'
              });
              return;
            }
            this.$buefy.toast.open({
              message: error.toString(),
              type: 'is-danger'
            });
          }).finally(() => {
        this.isLoading = false;
      });
    },
    editUser() {
      this.isLoading = true;
      api.put('/api/v1/user', {
        email: this.email,
        name: this.name,
        country: this.nameCountry,
        phoneNumber: this.phoneNumber === '' ? null : this.phoneNumber,
      })
          .then(() => {
            LocalStorageUtils.setName(this.name);
            document.getElementById("userName").textContent = this.name;
            this.$buefy.toast.open({
              message: 'Dados cadastrais atualizado com sucesso.',
              type: 'is-success'
            });
          })
          .catch(error => {
            if (error.response !== undefined) {
              this.$buefy.toast.open({
                message: error.response.data.message,
                type: 'is-danger'
              });
              return;
            }
            this.$buefy.toast.open({
              message: error.toString(),
              type: 'is-danger'
            });
          }).finally(() => {
        this.isLoading = false;
      });
    },
    changePassword() {
      this.isLoading = true;
      api.put('/api/v1/user/change-password', {
        password: this.newPassword,
      })
          .then(() => {
            this.newPassword = null;
            this.$buefy.toast.open({
              message: 'Senha atualizada com sucesso.',
              type: 'is-success'
            });
          })
          .catch(error => {
            if (error.response !== undefined) {
              this.$buefy.toast.open({
                message: error.response.data.message,
                type: 'is-danger'
              });
              return;
            }
            this.$buefy.toast.open({
              message: error.toString(),
              type: 'is-danger'
            });
          }).finally(() => {
        this.isLoading = false;
      });
    },
  },
  computed: {
    filteredDataCountryArray() {
      return this.dataCountry.filter((option) => {
        return option
            .toString()
            .toLowerCase()
            .indexOf(this.nameCountry.toLowerCase()) >= 0
      })
    }
  },
